@layer base {
  :root {
    --page-scroll-bg-on: linear-gradient(var(--root-bg), var(--root-bg))
    var(--root-bg);
    @supports (color: color-mix(in lab, red, red)) {
      --page-scroll-bg-on: linear-gradient(var(--root-bg), var(--root-bg))
    color-mix(in srgb, var(--root-bg), oklch(0% 0 0) calc(var(--page-has-backdrop, 0) * 40%));
    }
